@charset "UTF-8";
/**
 * Block Search
 * - SEE _search.forms.scss for detailed classes for block search form.
 ============================================================================ */
.pr-navbar .block-search {
  padding: 0.375rem;
}

@media all and (min-width: 60.063em) {
  .pr-navbar .block-search {
    transform: translateZ(0);
  }
  .pr-navbar .block-search .form-search {
    width: 6.25rem;
    transition-property: all;
    transition-duration: 0.2s;
    transition-timing-function: ease-out;
  }
  .pr-navbar .block-search .form-search:hover, .pr-navbar .block-search .form-search:focus {
    width: 9.375rem;
  }
}

@media all and (max-width: 60em) {
  .pr-navbar .block-search {
    padding: 0.375rem 0;
  }
  .pr-navbar .block-search[class*="pull-"] {
    float: none;
  }
}

@media all and (min-width: 60.063em) {
  .pr-navbar__rw:not(.rm-row) .block-search {
    text-align: right;
  }
  .pr-navbar__rw .rm-row .block-search {
    float: right;
  }
}

/**
 * Search results.
 ============================================================================ */
.item-list--search-results {
  border-radius: 0.125em;
}

.search-results {
  margin-bottom: 1rem;
  padding: 1rem;
}

.search-results--empty h3 {
  font-size: 1.25rem;
  line-height: 1.5rem;
}

.search-results__item {
  margin-bottom: 1.25rem;
  padding: 0;
  list-style: none;
}

.search-results__header {
  margin: 1.25rem 0;
  padding: 1.25rem 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
}

.search-results__header ul, .search-results__header dl, .search-results__header dd {
  margin: 0;
  padding: 0;
}

.search-results__header > div {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.search-results__header > div > * {
  margin-right: 1rem;
}

.search-results__header > div > *:last-of-type {
  margin-right: 0;
}

.search-results__header .pagination_option_label__container > * {
  display: flex;
}

.search-results__header .pagination_option_label__container > * .pagination_option_label {
  padding-right: 0.375rem;
}

.search-results__header .pagination_option_label__container > * .pagination_option_item {
  padding: 0 0.125rem;
}

.search-results__header .display_selector__container > * {
  padding: 0 0.75rem;
}

.facets-widget-links ul {
  padding-left: 0;
}

.facet-item {
  list-style-type: none;
}

.facet-item a {
  display: flex;
}

.facet-item__value {
  max-width: 100%;
  overflow: hidden;
}

.facet-item__count {
  padding-left: 0.25em;
}

.search-result__title {
  margin: 0;
}

.search-result__snippet-info {
  font-size: 0.875rem;
  line-height: 1.5rem;
}

.search-result p {
  margin: 0;
}

/**
 * Search forms
 * - search block form
 * - advanced search form
 ============================================================================ */
.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1,
.search-block-form__form {
  margin: 3.125em auto;
}

@media all and (min-width: 60.063em) {
  .block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1,
  .search-block-form__form {
    max-width: 50%;
  }
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 label,
.search-block-form__form label {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  word-wrap: normal;
  font: 0/0 a;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form--inline,
.search-block-form__form .form--inline {
  display: flex;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form--inline .form-item,
.search-block-form__form .form--inline .form-item {
  float: none;
  margin: 0;
  width: 100%;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form-type-search,
.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form-actions,
.search-block-form__form .form-type-search,
.search-block-form__form .form-actions {
  display: inline;
  margin: 0;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form-search,
.search-block-form__form .form-search {
  width: 100%;
}

.submit-is-visible .block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form-search, .submit-is-visible
.search-block-form__form .form-search {
  padding-right: 2.1875rem;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .form-actions .form-submit,
.search-block-form__form .form-actions .form-submit {
  border-radius: 0;
  margin-right: 0;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 .contextual-render-processed,
.search-block-form__form .contextual-render-processed {
  top: 2px;
  right: 2em;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 ::-webkit-input-placeholder,
.search-block-form__form ::-webkit-input-placeholder {
  color: #e5e5e5;
  opacity: 0.75;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 :-moz-placeholder,
.search-block-form__form :-moz-placeholder {
  color: #e5e5e5;
  opacity: 0.75;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 ::-moz-placeholder,
.search-block-form__form ::-moz-placeholder {
  color: #e5e5e5;
  opacity: 0.75;
}

.block.block-plugin-id--views-exposed-filter-block-solr-search-content-page-1 :-ms-input-placeholder,
.search-block-form__form :-ms-input-placeholder {
  color: #e5e5e5;
  opacity: 0.75;
}

.search-basic {
  display: block;
}

.search-basic label {
  display: block;
  margin: 0 0 0.5rem;
}

.search-basic .form-type-search,
.search-basic .form-search {
  display: block;
  width: 100%;
  margin: 0.5rem 0;
}

.search-advanced .details-wrapper > fieldset > legend {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  word-wrap: normal;
  font: 0/0 a;
}

.search-advanced .form-text {
  min-width: 75%;
}

.search-advanced .form-item,
.search-advanced .form-radios,
.search-advanced .form-checkboxes {
  margin-bottom: 0.5rem;
}

.search-help-link {
  float: right;
  font-size: 0.875rem;
  line-height: 1.5rem;
  margin-top: -1.5rem;
  padding-right: 0.375rem;
}

.search-help-link:before {
  font-family: FontAwesome, sans-serif;
  content: "ÔÅô";
  padding: 0;
  margin: 0;
  text-align: center;
  width: 1rem;
  display: none;
}

.fa-loaded .search-help-link:before {
  display: inline-block;
}

